.sign-inner-container {
    max-width: 400px !important;
    border-radius: 24px;
}

.sign-header-img {
    width: 64px;
    height: 64px;
}

@media only screen and (max-width: 768px) {
    .sign-inner-container {
        width: 90%;
    }
}

@media only screen and (min-width: 768px) {
    .sign-inner-container {
        width: 75%;
    }
}