}

/* width */

*::-webkit-scrollbar {
    width: 3px;
}


/* Track */

*::-webkit-scrollbar-track {
    background-color: rgb(0, 0, 0);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
}


/* Handle */

* ::-webkit-scrollbar-thumb {
    background: rgb(242, 6, 6);
}


/* Handle on hover */

*::-webkit-scrollbar-thumb:hover {
    background: rgb(255, 3, 3);
}
